html, body, #root, .main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background: #042943;
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  min-width: 450px;
  font-family: 'Source Sans Pro',Helvetica,Segoe,Arial,sans-serif;
  color: #505659;
}

header {
  min-height: 63px;
  height: 10%;
  width: 100%;
  display: inherit;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 1px solid rgba(255,255,255,.2);
}

header>div{
  color: rgba(255,255,255,1);
  font-size: 22px;
  padding-left: 6%;
}

#top-logo{
  margin-left: 20px;
}

#nav-dropdown{
  display: none;
}

.nav-tabs {
  font-size: 18px;
  margin-right: 20px;
  border-bottom: none;
}

.nav-tabs>li>a {
  color: rgba(255,255,255,.8);
  border: none;
}

.nav-tabs>li>a:hover {
  color: rgba(255,255,255,1);
  background-color: transparent;
  border-radius: 4px;
  border-color: #042943;
}

.nav-tabs>li.active>a {
  border-radius: 4px;
}

@media only screen and (max-width:800px){
  header>div{
    padding-left: 0%;
  }

  #nav-dropdown{
    display: block;
  }

  .nav-item{
    display: none !important;
  }


}



